body {
    padding: 50px;
    background-color: #66CCFF;
    font-family: sans-serif;
}

.TodoListApp .TodoListHeader input {
    padding: 10px;
    font-size: 16px;
    border: 2px solid #FFF;
}

.TodoListApp .TodoListHeader button {
    padding: 10px;
    font-size: 16px;
    margin: 10px;
    background-color: #0066FF;
    color: #FFF;
    border: 2px solid #0066FF;
    border-radius: 5px;
}

.TodoItems {
    list-style: none;
    padding-left: 0;
    font-size: 16px;
}

.TodoItems li {
    padding: 10px;
    margin: 10px 0 0 0;
    background-color : rgba(255,255,255,0.5);
    color: #000;
    width: 255px;
    border-radius: 5px;

}
